import {createBottomTabNavigator} from "@react-navigation/bottom-tabs";
import React from "react";
import {createNativeStackNavigator} from "@react-navigation/native-stack";
import {AppName} from "./AppNameConfig";
import {CssView} from "./components/BaseComponent";
import {SvgSource} from './assets/SvgSource';
import {Colors} from "./themes/colors";
import CustomerInvoiceScreen from "./screens/CustomerInvoiceScreen";
import InflowReport from "./screens/InflowReport";
import CustomerScreen from "./screens/CustomerScreen";
import InflowScreen from "./screens/InflowScreen";

const Tab = createBottomTabNavigator();
const Stack = createNativeStackNavigator();

const _route: any = {
    "Inflows": <CssView width={24} height={24}><SvgSource.iconExpenseReport /></CssView>,
    "Customer Invoices": <CssView width={24} height={24}><SvgSource.CustomerInvoice /></CssView>,
    "Inflow Reports": <CssView width={24} height={24}><SvgSource.InflowReport /></CssView>,
    "Customers": <CssView width={24} height={24}><SvgSource.Customer /></CssView>,
}

const _routeActive: any = {
    "Inflows": <CssView width={24} height={24}><SvgSource.iconExpenseReportActive /></CssView>,
    "Customer Invoices": <CssView width={24} height={24}><SvgSource.CustomerInvoiceActive /></CssView>,
    "Inflow Reports": <CssView width={24} height={24}><SvgSource.InflowReportActive /></CssView>,
    "Customers": <CssView width={24} height={24}><SvgSource.CustomerActive /></CssView>,
}


const BottomTabs = () => {
    return (
        <Tab.Navigator
            screenOptions={({route}) => ({
                tabBarActiveTintColor: Colors.primary,
                tabBarInactiveTintColor: Colors.grey3,
                headerShown: false,
                tabBarIcon: ({focused}) => {
                    if (focused) {
                        return _routeActive[route.name]
                    }
                    return _route[route.name]
                }
            })}>
            <Tab.Screen name="Inflow Reports" component={InflowReport}/>
            <Tab.Screen name="Customer Invoices" component={CustomerInvoiceScreen}/>
            <Tab.Screen name="Inflows" component={InflowScreen}/>
            <Tab.Screen name="Customers" component={CustomerScreen}/>
        </Tab.Navigator>
    );
};


const Main = () => {
    return (
        <Stack.Navigator>
            <Stack.Screen
                name={`${AppName}-BottomTabs`}
                component={BottomTabs}
                options={{headerShown: false}}
            />
            <Stack.Screen name={`${AppName}-Detail`} component={InflowScreen} />
        </Stack.Navigator>
    );
};

export default Main;
